<template>
    <div>
        <loading :active.sync="isLoading" ></loading> 
        <div class="text-right mt-4">
            <button class="btn btn-primary" data-toggle="modal" @click="openmodel(true)">建立新的優惠券</button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th >名稱</th>
                    <th width="200">折扣百分比</th>
                    <th width="200">到期日</th>
                    <th width="100">是否啟用</th>
                    <th width="130" >編輯</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in coupons" :key="item.id" >
                    <td>{{item.title}}</td>
                    <td>{{item.percent}}</td>
                    <td >{{item.due_date }}</td>
                    <td class="text-center">
                        <span v-if="item.is_enabled" class="text-success">已啟用</span>
                        <span v-else class="text-secondary">未啟用</span>
                    </td>
                    <td>
                        <div class="btn-group" role="group" aria-label="Basic example">
                            <button class="btn btn-outline-info btn-sm"
                            @click="openmodel(false,item)">編輯</button>
                            <button class="btn btn-danger btn-sm" 
                            @click="delModal(item)">刪除</button>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
        <!-- Pagination -->
        <!-- <Pagination :pagination="pagination" v-on:getPageProducts="getProducts"/> -->
        <div class="modal fade" id="couponModel" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <form>
                            <div class="form-group">
                                <label for="titleFormControl" >標題</label>
                                <input type="text" v-model="tempCoupon.title" class="form-control" id="titleFormControl">
                            </div>
                            <div class="form-group">
                                <label for="couponFormControl">優惠碼</label>
                                <input type="text" v-model="tempCoupon.code" class="form-control" id="couponFormControl">
                            </div>
                            <div class="form-group">
                                <label for="dateFormControl">到期日</label>
                                <input type="date" v-model="tempCoupon.due_date" class="form-control" id="dateFormControl">
                            </div>
                            <div class="form-group">
                                <label for="discountFormControl">折扣百分比</label>
                                <input type="number" v-model="tempCoupon.percent" class="form-control" id="discountFormControl">
                            </div>
                            <div class="form-group">
                                <div class="form-check">
                                <input class="form-check-input"  v-model="tempCoupon.is_enabled" type="checkbox" id="gridCheck">
                                <label class="form-check-label" for="gridCheck">
                                    是否啟用
                                </label>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>
                        <button type="button" @click="addCoupons" class="btn btn-primary">確認</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除 <strong class="text-danger">{{ tempCoupon.title }}</strong> (刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button @click="removeCoupon" type="button" class="btn btn-danger"
                    >確認刪除</button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import $ from 'jquery';
import Pagination from '../components/Pagination';

export default {
    data: function() {
        return {
            coupons : [],
            tempCoupon : {},
            isNew : false,
            isDelete : false,
            isLoading: false,
            pagination: {},
        };
    },
    components: {
        Pagination,
    },
    methods: {
        openmodel(isNew,item){
            if(isNew) {
                this.tempCoupon = {};
                this.isNew = true;
            }else {
                this.tempCoupon = Object.assign({},item);
                this.isNew = false;
                console.log(this.tempCoupon);
            }
            $('#couponModel').modal('show');
        },
        delModal(item){
            $('#delProductModal').modal('show');
            this.tempCoupon = Object.assign({},item);
        },
        addCoupons() {
            let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`;
            let httpMethod = 'post'
            const vm = this;
            if(!vm.isNew) {
                api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
                httpMethod = 'put'
            }
            this.$http[httpMethod](api,{data:vm.tempCoupon}).then((response) => {
                console.log(response.data);
                if(response.data.success){
                    $('#couponModel').modal('hide');
                    vm.getCoupons()
                }else {
                    $('#couponModel').modal('hide');
                    vm.getCoupons()
                    console.log('新增失敗')
                }
            });
        },
        getCoupons(page = 1) {
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`;
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
                vm.coupons = response.data.coupons;
                vm.isLoading = false;
                vm.pagination = response.data.pagination;
                // console.log(response.data);
            });
        },
        removeCoupon() {
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
            vm.isLoading = true;
            this.$http.delete(api,vm.tempCoupon).then((response) => {
                vm.isLoading = false;
                $('#delProductModal').modal('hide');
                vm.getCoupons();
            });
        },
    },
    created(){
        this.getCoupons();
    }
}
</script>
